html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 8rem;
}

h2 {
  font-size: 7rem;
}

h3 {
  font-size: 5rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #101010;
  font-size: 3rem;
  text-align: center;
}

.page-description {
  text-align: center;
}

.form {
  margin-top: 5rem;
}
.form .field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.form .field label {
  flex: 0 0 10rem;
}
.form .field input {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  background-color: #d8dadf;
}

.actions {
  margin-top: 3.5rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #101010;
}
.actions a:last-of-type {
  margin-bottom: 0;
}

.alert {
  padding: 1rem 1rem 1rem 2rem;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 1.4rem;
  max-width: 80rem;
}
.alert.error {
  background-color: #fcc4c8;
  border-left: 1rem solid rgb(184, 9, 21);
  border-radius: 1rem;
  color: rgb(184, 9, 21);
}
.alert.success {
  background-color: #cde5c1;
  border-left: 1rem solid #487631;
  border-radius: 1rem;
  color: #487631;
}

.login {
  margin-top: 10rem;
}
.login .uptask {
  background: linear-gradient(to right, #2563eb 40%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
  font-size: 8rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 12rem;
  }
}
.login input[type=submit] {
  background-color: #2563eb;
  padding: 1.5rem 4rem;
  color: #ffffff;
  border-radius: 1rem;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1.5rem 0;
  margin: 3.5rem 0 0;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #134cca;
  cursor: pointer;
}
.login .actions a {
  color: #2563eb;
}

