.dashboard {
  background-color: #e0e3ea;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .main {
  flex: 1;
  overflow: scroll;
}
.dashboard .content {
  padding: 2rem;
}
.dashboard .content .page-name {
  font-size: 4rem;
  text-align: center;
}
.dashboard .form input:not([type=submit]) {
  background-color: #ffffff;
}
.dashboard .form input[type=submit] {
  background-color: #500c81;
  padding: 1.5rem 4rem;
  color: #ffffff;
  border-radius: 1rem;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1.5rem 0;
  padding: 1rem;
  margin-top: 2.5rem;
  margin-bottom: 0;
  font-size: 1.5rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .dashboard .form input[type=submit] {
    width: auto;
  }
}
.dashboard .form input[type=submit]:hover {
  background-color: #330852;
  cursor: pointer;
}
.dashboard .link-div {
  text-align: center;
  margin-top: 5rem;
}
.dashboard .link {
  font-size: larger;
  font-weight: 900;
  color: #500c81;
}
.dashboard .link:hover {
  color: black;
}

.sidebar {
  opacity: 0;
  background-color: #500c81;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 600ms;
}
.sidebar.show {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(8, 57, 103, 0.95);
}
@media (min-width: 768px) {
  .sidebar.show {
    display: none;
  }
}
.sidebar.hide {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
    padding: 2rem;
    background-color: #500c81;
  }
}
.sidebar .container-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .container-sidebar h2 {
  color: #ffffff;
  margin: 0;
  padding: 2rem;
  text-align: center;
}
.sidebar .container-sidebar .close-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .container-sidebar .close-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .container-sidebar .close-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #ffffff;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 250ms;
  text-transform: uppercase;
  text-align: center;
  font-size: 2.2rem;
  font-weight: 700;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.active {
  background-color: #25053b;
  border-radius: 0.7rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.8rem;
    margin-bottom: 1.8rem;
    text-align: left;
  }
}
.sidebar .sign-off-mobile {
  margin-top: 8rem;
}
.sidebar .sign-off-mobile .sign-off {
  background-color: #0b94c2;
  padding: 1.5rem 4rem;
  color: #ffffff;
  border-radius: 1rem;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1.5rem 0;
}
@media (min-width: 768px) {
  .sidebar .sign-off-mobile .sign-off {
    width: auto;
  }
}
.sidebar .sign-off-mobile .sign-off:hover {
  background-color: #086f92;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .sign-off-mobile {
    display: none;
  }
}

