.task {
  padding: 1rem 2rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #c2c8d5;
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  align-items: center;
}
.task:last-of-type {
