}
.task p {
  font-size: 1.6rem;
}
.task p:hover {
  cursor: pointer;
}
.task .options {
  display: flex;
  gap: 1rem;
}
.task .options button {
  border: none;
  border-radius: 0.8rem;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
  padding: 0.5rem 1rem;
}
.task .options button:hover {
  cursor: pointer;
}
.task .options .completa {
  background-color: #2563eb;
  color: #ffffff;
}
.task .options .pendiente {
  background-color: #f59e0b;
  color: #ffffff;
}
.task .options .delete-task {
  background-color: rgb(184, 9, 21);
  color: #ffffff;
}

.modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal .form {
  max-width: 60rem;
  background-color: #ffffff;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in;
  opacity: 0;
  border-radius: 0.8rem;
}
.modal .form legend {
  font-size: 2rem;
  font-weight: 700;
  display: block;
  text-align: center;
  margin-bottom: 2rem;
}
.modal .form input[type=text] {
  border: 1px solid #6b7280;
}
.modal .form input[type=submit] {
  padding: 1.5rem;
  margin: 1rem 0;
}
.modal .form label {
  font-size: 2rem;
  font-weight: 700;
  display: block;
  text-align: center;
}
.modal .form .options {
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 480px) {
  .modal .form .options {
    display: flex;
    margin-top: 0;
  }
}
.modal .form .submit-new-task {
  background-color: #500c81;
  padding: 1.5rem 4rem;
  color: #ffffff;
  border-radius: 1rem;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1.5rem 0;
  font-size: 1.5rem;
  margin-top: 0;
}
@media (min-width: 768px) {
  .modal .form .submit-new-task {
    width: auto;
  }
}
.modal .form .submit-new-task:hover {
  background-color: #330852;
  cursor: pointer;
}
.modal .form .close-modal {
  background-color: #f11d1d;
  padding: 1.5rem 4rem;
  color: #ffffff;
  border-radius: 1rem;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1.5rem 0;
  font-size: 1.5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .modal .form .close-modal {
    width: auto;
  }
}
.modal .form .close-modal:hover {
  background-color: #ce0d0d;
  cursor: pointer;
}
.modal .form.animate {
  position: relative;
  top: 50%;
  transform: translateY(-100%);
  opacity: 1;
}
.modal .form.close {
  transform: translateY(-200%);
  opacity: 0;
}

.filters {
  background-color: #d1d5e0;
  border-radius: 0.8rem;
  padding: 2rem;
  margin-top: 1rem;
}
@media (min-width: 1024px) {
  .filters .filters-inputs {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.filters h2 {
  color: #500c81;
  font-size: 2.8rem;
}
@media (min-width: 1024px) {
  .filters h2 {
    margin: 0;
  }
}
.filters .field {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .filters .field {
    margin-bottom: 0rem;
  }
}
.filters .field label {
  color: #81214c;
  font-size: large;
  font-weight: 700;
}
.filters input[type=radio] {
  accent-color: #500c81;
  width: 2rem;
  height: 1.5rem;
}

@media (prefers-color-scheme: dark) {
  body,
.dashboard {
    background-color: #2f3135;
  }

  .tagline,
.page-description,
.page-name,
.task p,
.no-tasks,
.form label,
.dashboard .link {
    color: #e0e3ea;
  }

  .form .field input,
.dashboard .form input:not([type=submit]) {
    background-color: #c2c8d5;
    color: #101010;
  }

  .login .actions a,
.create .actions a {
    color: #b1c7f8;
