  }

  .sidebar {
    background-color: #232527;
  }
  .sidebar.show {
    background-color: #232527;
  }

  .bar,
.tasks-list,
.modal .form,
.filters {
    background-color: #333a4b;
  }
  .bar h2,
.bar p,
.bar .field label,
.bar legend,
.tasks-list h2,
.tasks-list p,
.tasks-list .field label,
.tasks-list legend,
.modal .form h2,
.modal .form p,
.modal .form .field label,
.modal .form legend,
.filters h2,
.filters p,
.filters .field label,
.filters legend {
    color: #e0e3ea;
  }

  .bar p span {
    color: #a3acc1;
    font-size: large;
  }

  .tasks-list {
    border-radius: 0.5rem;
  }
  .tasks-list .task {
    border-bottom: none;
  }

  .no-projects {
    color: #e0e3ea;
  }
}