@use "base/variables" as v;
@use "base/mixins" as m;

.form {
    margin-top: 5rem;
  .field {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;

    label {
        flex: 0 0 10rem;
    }
  
    input {
        flex: 1;
        padding: 1rem;
        border: none;
        border-radius: 1rem;
        background-color: lighten(v.$gray, 40%);
    }
  }
}
