@use 'base/mixins' as m;
@use 'base/variables' as v;

.actions {
    margin-top: 3.5rem;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    a {
        display: block;
        text-align: center;
        margin-bottom: 2rem;
        color: v.$black;
        &:last-of-type{
            margin-bottom: 0;
        }
    }
}
